import React, {useCallback, useMemo} from 'react';
import {useState} from "react";


export default {
    title: 'React Memo',

}



export const ReactMemo = () => {

    const [count, setCount] = useState(0)

    const [user, setUser] = useState(['Dimych', 'Valera', 'Julian'])

    const MainRerender =useMemo(()=>{return user.filter((el)=>el.toLocaleLowerCase().indexOf('i')>-1)},[user])


    const  addCount =()=> {
        setCount(count+1)
    }

    const memorized=useCallback(()=>{ setUser([...user,'Marty'+' ' + Date()])},[user])

    return (

        <>

            <button onClick={memorized}>Rerender user</button>
        </>
    )
};










